import { ProfilePanel } from './ProfilePanel';
import { locales } from './Locales';

const av = Autodesk.Viewing;

const BUTTON_LABEL = 'Profiles';

/**
 * Provides UI controls for Profiles
 * 
 * The extension id is: `Autodesk.ProfileUi`
 * 
 * @example
 *    viewer.loadExtension('Autodesk.ProfileUi');
 * 
 * @see {@link Autodesk.Viewing.Extension} for common inherited methods.
 * @constructor
 * @param {Autodesk.Viewing.Viewer3D} viewer - Viewer instance.
 * @param {Object} options - Not used.
 */
export function ProfileUi(viewer, options) {
    Autodesk.Viewing.Extension.call(this, viewer, options);
    this.viewer = viewer;
    this._configButtonId = null;
    this._panel = null;
    this.openPanel = this.openPanel.bind(this);
    this._initButtonConfig = this._initButtonConfig.bind(this);
}

ProfileUi.prototype = Object.create(Autodesk.Viewing.Extension.prototype);
ProfileUi.prototype.constructor = ProfileUi;

/**
 * Load the extensions
 */
ProfileUi.prototype.load = function () {
    this.extendLocalization(locales);
    return true;
};

/**
 * Unload the extension.
 */
ProfileUi.prototype.unload = function () {        
    if (this._configButtonId !== null) {
      this.viewer.viewerSettingsPanel.removeConfigButton(this._configButtonId);
      this._configButtonId = null;   
    }
    if (this._panel) {
      this._panel.uninitialize();
      this._panel = null;
    }
    return true;
};

/**
 * Invoked by the viewer when the toolbar UI is available. 
 * Adds a button to the Settings panel.
 *
 * @alias Autodesk.Viewing.Extensions.ProfileUi#onToolbarCreated
 */
ProfileUi.prototype.onToolbarCreated = function() {   
    this._initButtonConfig();
};

/**
 * Opens the Profiles panel.
 *
 * @alias Autodesk.Viewing.Extensions.ProfileUi#openPanel
 */
ProfileUi.prototype.openPanel = function() {
    if (!this._panel) {
        this._panel = new ProfilePanel(this.viewer, BUTTON_LABEL);
    }
    this._panel.setVisible(true);
};

/**
 * Adds a button to the Settings Panel.
 * @private
 */
ProfileUi.prototype._initButtonConfig = function() {   
    var settingsPanel = this.viewer.getSettingsPanel();
    if (!settingsPanel) {
        this.addEventListener(av.SETTINGS_PANEL_CREATED_EVENT, this._initButtonConfig, {once: true});
        return;
    }
    this._configButtonId = settingsPanel.addConfigButton(BUTTON_LABEL, this.openPanel);
};


/**
 * Register the extension with the extension manager.
 */
Autodesk.Viewing.theExtensionManager.registerExtension('Autodesk.ProfileUi', ProfileUi);
