const TAB_ID = 'configTab';
const TAB_LABEL = 'Configuration';
const DEFAULT_DESC = 'Description not available';

/**
 * Retrieves all of the unique profiles that have been registered.
 * @param {Object} registeredProfiles - viewer.profileManager.getProfiles()
 * @returns {Autodesk.Viewing.Profile[]} - array of viewer unique profiles
 * @private
 */
function getUniqueProfiles(registeredProfiles) {
    const profiles = [];
    for (let key in registeredProfiles) {
        const profile = registeredProfiles[key];
        if (profiles.indexOf(profile) === -1) {
            profiles.push(profile);
        }
    }
    return profiles;
}

var ProfilePanel = function(viewer, title) {
    
    const PANEL_ID = 'adsk_profile_panel_' + viewer.id;
    const opts = { heightAdjustment: 100, width: 460, hideTabBar: true };
    Autodesk.Viewing.UI.SettingsPanel.call(this, viewer.container, PANEL_ID, title, opts);

    this.container.classList.add('viewer-settings-panel');
    this.viewer = viewer;
    this.setGlobalManager(viewer.globalManager);

    this.addTab(TAB_ID, TAB_LABEL, { className: 'config' });
    this.selectTab(TAB_ID);

    this._profiles = getUniqueProfiles(this.viewer.profileManager.getProfiles());
    this._onClick = this._onClick.bind(this);
    this._onProfileChanged = this._onProfileChanged.bind(this);

    var listContainer = this.tablesContainer.childNodes[0];
    this._simpleList = this.addSimpleList(listContainer, renderListItem, this._onClick);
    this._updateListUi();

    this.viewer.addEventListener(Autodesk.Viewing.PROFILE_CHANGE_EVENT, this._onProfileChanged);
};

ProfilePanel.prototype = Object.create(Autodesk.Viewing.UI.SettingsPanel.prototype);
ProfilePanel.prototype.constructor = ProfilePanel;

/**
 * @private
 */
ProfilePanel.prototype._onClick = function(index) {
    const profile = this._profiles[index];
    this.viewer.setProfile(profile);
};

/**
 * Appends a cell to the Profile grid.
 * @param {*} event - should contain event.profile
 * @returns {boolean} - returns true if a grid cell was added.
 * @private
 */
ProfilePanel.prototype._onProfileChanged = function(event) {
    const profile = event && event.profile;
    if (!profile) return false;
    if (this._profiles.indexOf(profile) === -1) {
        this._profiles.push(profile);
    }
    this._updateListUi();
};

/**
 * Updates the table grid ui.
 * @private
 */
ProfilePanel.prototype._updateListUi = function() {
    // Create a new array of from the this._profiles array.
    // This will update the description if it is not present.
    const uiItems = this._profiles.map((profile) => {
        return {
            name: profile.label || profile.name,
            description: profile.description || DEFAULT_DESC
        };
    });
    
    var selectedIndex = this._profiles.indexOf(this.viewer.profile);
    this._simpleList.setData(uiItems, selectedIndex);

    // Resize the panel for the new cell.
    this.resizeToContent();
};

/**
 * Uninitialize the panel.
 * Removes all event listeners associated with the panel.
 */
ProfilePanel.prototype.uninitialize = function() {
    Autodesk.Viewing.UI.SettingsPanel.prototype.uninitialize.call(this);
    this.viewer.removeEventListener(Autodesk.Viewing.PROFILE_CHANGE_EVENT, this._onProfileChanged);
    this.removeSimpleList(this._simpleList);
    this._simpleList = null;
};

export { ProfilePanel };

/**
 * Populates the DOM list element with all the necessary UI to
 * display a Profile's info the end users.
 *
 * @param {HTMLElement} domElem - The DOM element created by SimpleList.
 * @param {Object} uiItem - The list item to render by domElem.
 * @param {Object} options - Bag of options.
 *
 * @private
 */
function renderListItem(domElem, uiItem, options) {

    var _document = options.domDocument;
    const i18n = Autodesk.Viewing.i18n;

    domElem.classList.add("settings-cell");

    // Name
    const name = _document.createElement("span");
    name.textContent = i18n.translate(uiItem.name);
    name.classList.add("settings-name");
    name.setAttribute('data-i18n', uiItem.name);
    domElem.appendChild(name);

    const description = _document.createElement("span");
    description.classList.add("settings-description");
    description.textContent = !uiItem.description ? '' : i18n.translate(uiItem.description);
    description.setAttribute('data-i18n', uiItem.description);
    domElem.appendChild(description);
}

